/* oranienbaum-regular - latin_cyrillic */
@font-face {
  font-display: swap;
  font-family: 'Oranienbaum';
  font-style: normal;
  font-weight: 400;
  src: url('../fonts/oranienbaum-v15-latin_cyrillic-regular.woff2')
      format('woff2'),
    url('../fonts/oranienbaum-v15-latin_cyrillic-regular.woff') format('woff');
}

